/* eslint-disable no-unused-vars */

import { useState, useCallback } from "react";
import style from "../css/Authentication.module.css";
import Cookies from "js-cookie";
import { Link } from "react-router-dom";

/*Redux*/
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { bindActionCreators } from "redux";
import { actionCreators } from "../../state/index";
/***** */

function Authentication() {
  const [isLoginPanle, setLoginPanle] = useState(true);
  const setLoginPanleTrue = useCallback(() => {
    setLoginPanle(true);
  }, []);
  return (
    <div className={style.AuthWrapper}>
      <div className={style.TextWrapper}>
        <h1>Lorem ipsum dolor consectetur</h1>
        <p>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam
          tellus, viverra scelerisque massa.
        </p>
      </div>
      <div className={style.Auth}>
        <div className={style.AuthPanel}>
          <div className={style.Authheader}>
            {isLoginPanle ? <h1>Login</h1> : <h1>Regester</h1>}
          </div>
          {isLoginPanle ? (
            <LoginPanel />
          ) : (
            <RegesterPanel setLoginPanle={() => setLoginPanleTrue()} />
          )}
        </div>
        <div className={style.switcher}>
          <ul>
            <li
              className={
                isLoginPanle
                  ? `${style.switer_li} ${style.active}`
                  : `${style.switer_li} `
              }
              onClick={() => setLoginPanle(true)}
            >
              <p>Login</p>
            </li>
            <li
              className={
                !isLoginPanle
                  ? `${style.switer_li} ${style.active}`
                  : `${style.switer_li} `
              }
              onClick={() => setLoginPanle(false)}
            >
              <p>Regester</p>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
}
const LoginPanel = () => {
  const baseUri = "https://generateplatformpdf.herokuapp.com/Authentication/";

  const [loginInfo, setlogininfo] = useState({
    email: "",
    password: "",
  });

  const state = useSelector((state) => state.User);

  const dispatch = useDispatch();

  const { Login, SetUserInfo } = bindActionCreators(actionCreators, dispatch);

  const loginSubmite = async (e) => {
    e.preventDefault();

    console.log(state);
    fetch(baseUri, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(loginInfo),
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.token !== false) {
          fetch(`${baseUri}GetUser`, {
            method: "POST",
            headers: new Headers({
              autherization: "Bearer " + data.token,
              "Content-Type": "application/json",
            }),
          })
            .then((res) => {
              return res.json();
            })
            .then((ResData) => {
              SetUserInfo({
                username: ResData.Username,
                Email: ResData.Email,
                Profile: "https://uifaces.co/our-content/donated/XdLjsJX_.jpg",
                Subscription: ResData.Subscription,
              });
              Login();

              Cookies.set("user", data.token, { expires: 2 });
            });
        } else {
        }
      })
      .catch((error) => {
        console.error("Error:", error);
      });
  };
  return (
    <div className={style.login}>
      <form
        method="post"
        className={style.authForm}
        onSubmit={(e) => loginSubmite(e)}
      >
        <p className={style.inputlabel}>E-mail</p>
        <input
          type="text"
          placeholder="Example@E-mail.com"
          value={loginInfo.email}
          onChange={(e) => {
            setlogininfo({ ...loginInfo, email: e.target.value });
          }}
        />
        <p className={style.inputlabel}>Password</p>
        <input
          type="password"
          placeholder="type your password"
          required
          value={loginInfo.password}
          onChange={(e) => {
            setlogininfo({ ...loginInfo, password: e.target.value });
          }}
        />
        <div className={style.option}>
          <div className="">
            <input type="checkbox" />
            <p>remember me</p>
          </div>
          <Link to="/Config_account">
            <p>Forgot my password</p>
          </Link>
        </div>
        <input type="submit" value="Login" />
      </form>
    </div>
  );
};

//Regester
const RegesterPanel = ({ setLoginPanle }) => {
  //States
  const [userInfo, setAuthUserInfo] = useState({
    Username: "",
    email: "",
    password: "",
    Subscription: 0,
    societe: "",
    Autre_societe: "",
    rasion_social: "",
    adress: "",
    tele_gerant: "",
    ballet_notifecation: "",
  });
  const [Currentpage, setcurrentpage] = useState(1);

  const RegesterRequest = (e) => {
    e.preventDefault();
    const Uri = `https://generateplatformpdf.herokuapp.com/Authentication/Regester`;
    fetch(Uri, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(userInfo),
    })
      .then((res) => {
        return res.json();
      })
      .then((data) => {
        console.log(data);
        if (data === true) {
          setLoginPanle();
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <div className={style.Regester}>
      <form
        method="post"
        className={style.authForm}
        onSubmit={(e) => RegesterRequest(e)}
      >
        <div className={style.pages}>
          <div
            className={`${style.page} ${style.page1} ${
              Currentpage === 1 && `${style.show}`
            }`}
          >
            <ul>
              <li className={style.inputContainer}>
                {" "}
                <p className={style.inputlabel}>Name</p>
                <input
                  type="text"
                  placeholder="Your Name"
                  value={userInfo.Username}
                  onChange={(e) => {
                    setAuthUserInfo({ ...userInfo, Username: e.target.value });
                  }}
                />
              </li>
              <li className={style.inputContainer}>
                {" "}
                <p className={style.inputlabel}>Email</p>
                <input
                  type="email"
                  placeholder="EXapmle@E-mail.com"
                  required
                  value={userInfo.email}
                  onChange={(e) => {
                    setAuthUserInfo({ ...userInfo, email: e.target.value });
                  }}
                />
              </li>
              <li className={style.inputContainer}>
                {" "}
                <p className={style.inputlabel}>Password</p>
                <input
                  type="password"
                  placeholder="type your password"
                  required
                  value={userInfo.password}
                  onChange={(e) => {
                    setAuthUserInfo({ ...userInfo, password: e.target.value });
                  }}
                />
              </li>
            </ul>

            <input
              type="button"
              value="Next"
              onClick={() => setcurrentpage(2)}
            />
          </div>
          <div
            className={`${style.page} ${style.page2} ${
              Currentpage === 2 && `${style.show}`
            }`}
          >
            <ul>
              <li className={style.inputContainer}>
                <p className={style.inputlabel}>societe</p>
                <select
                  required
                  value={userInfo.societe}
                  onChange={(e) => {
                    setAuthUserInfo({ ...userInfo, societe: e.target.value });
                  }}
                >
                  <option value="0">Select your Socite</option>
                  <option value="1">expert comptable</option>
                  <option value="2">center d'affaire </option>
                  <option value="3">comptable</option>
                  <option value="4">autre</option>
                </select>
              </li>
              {userInfo.societe == 4 && (
                <li className={style.inputContainer}>
                  {" "}
                  <p className={style.inputlabel}>societe</p>
                  <input
                    type="text"
                    placeholder="Votre socite"
                    value={userInfo.Autre_societe}
                    onChange={(e) => {
                      setAuthUserInfo({
                        ...userInfo,
                        Autre_societe: e.target.value,
                      });
                    }}
                  />
                </li>
              )}
              <li className={style.inputContainer}>
                {" "}
                <p className={style.inputlabel}>raisan sociale</p>
                <input
                  type="text"
                  value={userInfo.raisan_sociale}
                  onChange={(e) => {
                    setAuthUserInfo({
                      ...userInfo,
                      raisan_sociale: e.target.value,
                    });
                  }}
                />
              </li>
              <li className={style.inputContainer}>
                {" "}
                <p className={style.inputlabel}>adress</p>
                <input
                  type="text"
                  required
                  value={userInfo.adress}
                  onChange={(e) => {
                    setAuthUserInfo({ ...userInfo, adress: e.target.value });
                  }}
                />
              </li>
            </ul>
            <div className={style.RsgesterOption}>
              <input
                type="button"
                value="Previouse"
                onClick={() => setcurrentpage(1)}
              />
              <input type="submit" value="Regester" />
            </div>
          </div>
        </div>
      </form>
    </div>
  );
};

export default Authentication;
